import React from 'react';
import {
    Container,
    Row,
    Col,
    Card,
    CardBody,
    CardTitle,
    CardText,
    Button,
    Alert,
    Pagination,
    PaginationItem,
    PaginationLink,
    ListGroup,
    ListGroupItem,
    Table
} from 'reactstrap';

const ThemePage: React.FC = () => {
    return (
        <Container className="mt-5">
            <h1 className="text-center">Certavue Theme Overview</h1>
            <p className="lead text-center">
                This page demonstrates the custom theme styling along with a
                variety of standard HTML and Reactstrap components.
            </p>

            {/* Standard HTML Elements */}
            <section className="mb-5">
                <h2>Standard HTML Elements</h2>
                <h3>Headings</h3>
                <h4>Subheading</h4>
                <p>
                    This is a sample paragraph with <strong>bold text</strong>,{' '}
                    <em>italic text</em>, and an inline{' '}
                    <code>code snippet</code>.
                </p>
                <ul>
                    <li>Unordered list item 1</li>
                    <li>Unordered list item 2</li>
                    <li>Unordered list item 3</li>
                </ul>
                <ol>
                    <li>Ordered list item 1</li>
                    <li>Ordered list item 2</li>
                    <li>Ordered list item 3</li>
                </ol>
                <blockquote>
                    <p>
                        This is a blockquote. It is used to highlight quotations
                        or important statements.
                    </p>
                </blockquote>
                <hr />
                <p>
                    Check out this <a href="#!">sample link</a> and see how it
                    fits in the theme.
                </p>
            </section>

            {/* Custom Utility Classes Examples */}
            <section className="mb-5">
                <h2>Custom Utility Classes</h2>
                <div className="p-3 mb-3 bg-moon-raker text-steel-gray">
                    <p>
                        This div uses <code>.bg-moon-raker</code> for its
                        background and <code>.text-steel-gray</code> for its
                        text color.
                    </p>
                </div>
                <div className="p-3 mb-3 bg-lavender text-moon-raker">
                    <p>
                        This div uses <code>.bg-lavender</code> for its
                        background and <code>.text-moon-raker</code> for its
                        text color.
                    </p>
                </div>
                <div className="p-3 mb-3 bg-jagged-ice text-fountain-blue">
                    <p>
                        This div uses <code>.bg-jagged-ice</code> for its
                        background and <code>.text-fountain-blue</code> for its
                        text color.
                    </p>
                </div>
            </section>

            {/* Reactstrap Components */}
            <section className="mb-5">
                <h2>Reactstrap Components</h2>
                <Row>
                    <Col md={6}>
                        <Card className="mb-4">
                            <CardBody>
                                <CardTitle tag="h5">Card Title</CardTitle>
                                <CardText>
                                    This is a card with some example text to
                                    show how content looks. The card utilizes
                                    the custom theme colors.
                                </CardText>
                                <Button color="primary">Primary Button</Button>
                            </CardBody>
                        </Card>
                    </Col>
                    <Col md={6}>
                        <Alert color="warning">
                            This is an alert component with the default
                            Bootstrap warning styling.
                        </Alert>
                    </Col>
                </Row>

                <Row className="mb-4">
                    <Col>
                        <h4>Pagination</h4>
                        <Pagination>
                            <PaginationItem>
                                <PaginationLink previous href="#" />
                            </PaginationItem>
                            <PaginationItem active>
                                <PaginationLink href="#">1</PaginationLink>
                            </PaginationItem>
                            <PaginationItem>
                                <PaginationLink href="#">2</PaginationLink>
                            </PaginationItem>
                            <PaginationItem>
                                <PaginationLink href="#">3</PaginationLink>
                            </PaginationItem>
                            <PaginationItem>
                                <PaginationLink next href="#" />
                            </PaginationItem>
                        </Pagination>
                    </Col>
                </Row>

                <Row className="mb-4">
                    <Col>
                        <h4>List Group</h4>
                        <ListGroup>
                            <ListGroupItem>Item 1</ListGroupItem>
                            <ListGroupItem>Item 2</ListGroupItem>
                            <ListGroupItem>Item 3</ListGroupItem>
                        </ListGroup>
                    </Col>
                </Row>

                <Row className="mb-4">
                    <Col>
                        <h4>Table</h4>
                        <Table striped bordered>
                            <thead>
                                <tr>
                                    <th>#</th>
                                    <th>First Name</th>
                                    <th>Last Name</th>
                                    <th>Username</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <th scope="row">1</th>
                                    <td>Mark</td>
                                    <td>Otto</td>
                                    <td>@motto</td>
                                </tr>
                                <tr>
                                    <th scope="row">2</th>
                                    <td>Jacob</td>
                                    <td>Thornton</td>
                                    <td>@fat</td>
                                </tr>
                                <tr>
                                    <th scope="row">3</th>
                                    <td>Larry</td>
                                    <td>the Bird</td>
                                    <td>@twitter</td>
                                </tr>
                            </tbody>
                        </Table>
                    </Col>
                </Row>
            </section>
        </Container>
    );
};

export default ThemePage;
